// Navigation helpers for query-based routing
import { basePath } from './base-path';

export function navigateToEditor(projectId?: string) {
  const url = projectId 
    ? `${basePath}/editor?project=${encodeURIComponent(projectId)}`
    : `${basePath}/editor`;
  window.location.href = url;
}

export function navigateToProjects() {
  window.location.href = `${basePath}/projects`;
}

export function navigateToHome() {
  window.location.href = basePath || '/';
}

// Helper to build editor URL with project ID
export function getEditorUrl(projectId: string): string {
  return `${basePath}/editor?project=${encodeURIComponent(projectId)}`;
}

// Helper to build projects URL
export function getProjectsUrl(): string {
  return `${basePath}/projects`;
}